.page-footer {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #000000;
}

.page-footer__map {
  background-image: url("../img/bg-map-mobile@1x.png");
  width: 100%;
  height: 400px;
  position: relative;
  z-index: 1;
  background-position: 50% 0;
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
    background-size: 320px auto;
  }
}

.page-footer__pin {
  right: -3px;
  top: 177px;
  width: 55px;
  height: 53px;
  position: relative;
  background-image: url("../img/map-pin-png-50.png");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: 2;
}

.page-footer__contacts {
  display: flex;
  position: relative;
  background-color: #ffffff;
  margin-bottom: -40px;
  margin-top: -6px;
  z-index: 2;
}

.contacts__address-city {
  display: inline-block;
}

.page-footer__contacts--bg {
  background-color: #f2f2f2;
  padding-top: 27px;
  z-index: 2;
}

.contacts__invite {
  @include list-reset;
  font-size: 16px;
  line-height: 20px;
  color: #111111;
  text-transform: uppercase;
  margin-right: 23px;
  margin-bottom: 27px;
}

.contacts__address {
  @include list-reset;
  font-family: "ArialMT", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
}

.page-footer__logo-image {
  margin-top: 40px;
  margin-left: 76px;
  margin-bottom: 29px;
  position: relative;

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}

.page-footer__bg {
  background-color: #f2f2f2;
}

@media (min-width: $tablet-width) {
  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 30px;
  }

  .page-footer {
    align-items: center;
  }

  .page-footer__logo-image {
    margin-top: 25px;
    margin-left: 12px;
    margin-right: 26px;
  }

  .page-footer__map {
    background-image: url("../img/bg-map-tablet@1x.png");
    width: 100%;
    height: 400px;
    position: relative;
    z-index: 1;
    background-position: 50% 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
      background-size: 768px auto;
    }
  }

  .page-footer__pin {
    right: -6px;
    top: 150px;
    width: 113px;
    height: 106px;
    background-image: url("../img/map-pin.png");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/map-pin-png-50@2x.png");
      background-size: 113px auto;
    }
  }

  .page-footer__contacts {
    margin-bottom: 30px;
    margin-top: 52px;
  }

  .page-footer__contacts--bg {
    margin-bottom: 0;
    margin-top: 56px;
    padding-top: 64px;
    padding-bottom: 31px;
  }

  .contacts__invite {
    font-size: 20px;
    line-height: 25px;
    margin-left: 53px;
    margin-right: 315px;
    text-align: left;
  }

  .contacts__address {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    margin-right: 50px;
  }

  .contacts__address-city {
    display: inline-block;
    margin-bottom: 19px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__map {
    background-image: url("../img/bg-map-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: 50% 0;
    width: 100%;
    height: 400px;
    position: relative;
    z-index: 1;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
      background-size: 1440px auto;
    }
  }

  .page-footer__pin {
    top: 155px;
    right: -340px;
    width: 113px;
    height: 106px;
    background-image: url("../img/map-pin.png");
  }

  .page-footer__contacts {
    margin: 0 auto;
    display: flex;
    position: relative;
    width: 570px;
    height: 201px;
    top: 300px;
    left: -327px;
  }

  .contacts__invite {
    font-weight: 400;
    margin-left: 0;
    margin-right: 92px;
    padding-left: 56px;
    padding-top: 62px;
  }

  .contacts__invite--catalog {
    padding-top: 0;
  }

  .contacts__address {
    text-align: left;
    padding-top: 60px;
  }

  .contacts__address--catalog {
    padding-top: 0;
  }

  .page-footer {
    margin-top: -219px;
  }

  .page-footer__logo-image {
    margin-left: 17px;
  }
}
